/* @tailwind base;
@tailwind components;
@tailwind utilities; */

* {
  margin: 0;
  padding: 0;
  font-family: "girloy";
  font-size: 14px;
}

body {
  background-color: #000000e8;
}

/* .background {
  position: relative;
} */

h1 {
  color: #ffffff2e;
  font-weight: 600;
  font-size: 170px;
  letter-spacing: 8px;
}

::selection {
  color: aqua;
}

.card-container {
  height: 87vh;
}

.company-name {
  padding: 20px 0;
  user-select: none;
}

.company-name h4 {
  color: #ffffff7d;
  text-align: center;
  font-size: 20px;
  font-weight: 700;
  font-family: cursive;
}

.inner-bg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -2;
  user-select: none;
}

.card {
  background: #00000061;
  display: inline-block;
  width: 180px;
  height: 220px;
  border-radius: 30px;
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
  color: #ffffffc4;
  margin: 20px 20px 0 0;
}

.make-flex {
  display: flex;
}

.space-between {
  justify-content: space-between;
  align-items: center;
}

.bottom-part {
  position: absolute;
  bottom: 0;
  width: 100%;
}

.green {
  background: #24ad41;
}

.blue {
  background: #0346ff;
}

.top-part {
  padding: 20px 25px 0;
}

.size-panel {
  padding: 10px 25px 15px;
}

.download-panel {
  width: 100%;
  padding: 0 25px 0;
}

.icon {
  padding-bottom: 15px;
}

.download-panel p {
  color: white;
  text-align: center;
  padding: 15px 0;
  cursor: pointer;
}

.icon-bg {
  background: #292929;
  border-radius: 30px;
  padding: 6px 6px 2px;
  cursor: pointer;
  color: white;
}

.description {
  font-size: 12px;
  line-height: 20px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

.main{
  position: relative;
  overflow: hidden;
}

.addFixed {
  position: fixed;
  bottom: 20px;
  right: 20px;
}

.addBtn {
  padding: 10px;
  background: transparent;
  outline: none;
  border: 2px solid white;
  border-radius: 50px;
  color: white;
  line-height: 0;
  cursor: pointer;
}

@media screen and (max-width: 450px) {
  h1 {
    font-size: 80px;
  }

  .card {
    width: 160px;
    height: 240px;
    margin: 10px;
}

.card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

}
